@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;600&display=swap);
* {
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
  color: #393c41;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  overflow-x: hidden;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}

@-webkit-keyframes animateDown {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }
  60% {
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
  }
}

@keyframes animateDown {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
  }
  60% {
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
  }
}

/* ////SUB sections BEGIN///////// */
.clouddata,
.websites,
.applications,
.softwares,
.contact {
  margin-top: 70px;
  position: relative;
}

.application_banner {
  background: url("/assets/sdbnew1banner.png") no-repeat center center fixed;
  background-size: cover;
}
/* .websites header {
  height: 100vh;
} */
.clouddata header,
.websites header,
.applications header,
.softwares header,
.quote header {
  height: 100vh;
}
.header_content {
  position: absolute;
  top: 150px;
  right: 35%;
}
.header_content > button {
  width: 400px;
  background: white;
  height: 50px;
  border-radius: 10px;
  font-size: 25px;
  letter-spacing: 6px;
  cursor: pointer;
  border: none;
  color: #ffcb2b;
}
.header_content > button:hover {
  background: transparent;
  color: white;
  border: 1px solid gray;
}
.left {
  display: flex;
  padding: 10px;
  justify-content: space-between;
}
.left p {
  border-radius: 10px;
  background: gray;
  color: greenyellow;
  padding: 5px 10px 0 10px;
  height: 40px;
}
.subnav {
  position: -webkit-sticky;
  position: sticky;
  top: 68px;
  background: #64a7de;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.subnav div > h2 {
  font-size: 1.5em;
}
.subnav a:hover,
.subnav a > h2:hover {
  background: gray;
  border-radius: 5px;
  height: 25px;
  padding: 3px;
  opacity: 0.65;
}
.clouddata h2,
.applications h2,
.websites h2,
.softwares h2 {
  text-align: center;
  margin: 5px;
  padding: 10px;
  font-size: 2.5em;
}
.clouddata-contents,
.applications-contents {
  display: flex;
  padding: 10px;
}
.content-right {
  line-height: 25px;
  max-width: 981px;
}
.content-right p {
  width: 920px;
  padding-bottom: 5px;
}
.content-right h4 {
  padding: 20px 0 5px 0;
  font-size: 19px;
  font-style: italic;
}
.content-right h3 {
  background: white;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid rgb(29, 26, 26);
  width: 30%;
  text-align: center;
  margin-top: 10px;
  font-weight: 500;
}
.content-right h3:hover {
  background: rgb(50, 55, 55);
  color: whitesmoke;
}
.content-left {
  text-align: center;
  padding: 0 10px;
}
.content-left h3 {
  padding-bottom: 25px;
}
.subItems {
  padding-top: 10px;
  margin-top: 20px;
}
.card_container {
  display: flex;
  justify-content: space-evenly;
  margin-top: 30px;
  padding-top: 10px;
}
.card {
  width: 200px;
  border: 1px solid gray;
  border-radius: 10px;
  text-align: center;
  transition: 0.5s;
}
.card:hover {
  -webkit-transform: translateY(-10%);
          transform: translateY(-10%);
}
.card p {
  max-width: 70%;
  font-size: 12px;
  text-align: justify;
  margin-left: 25px;
  line-height: normal;
}
.card_divider {
  height: 1px;
  background: red;
  margin: 2px 0 5px 0;
}
.card img {
  width: 150px;
  height: 150px;
}
