  }
  .card_container {
    flex-direction: column;
    align-items: center;
  }
  .card {
    margin: 10px;
  }
  .card a > p {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  .content-left {
    margin-top: 30px;
  }
  .content_img {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .contact_header {
    flex-direction: column;
  }
  .contact_input {
    flex-direction: column;
    margin: 0;
  }
  .contact_input input {
    width: 100%;
    margin: 5px;
  }
  .contact_form button {
    width: 100%;
    margin: 5px;
    font-size: 12px;
  }
  .contact_form textarea {
    width: 100%;
    margin: 5px;
  }
  .contact_form form {
    flex: 1 1;
  }
  .description {
    font-size: 1.3rem;
    margin: 5px;
    text-align: center;
    font-weight: normal;
  }
  .headercontent {
    margin: 5px;
    border: 1px solid red;
    border-radius: 5px;
    padding: 3px;
  }
  .divider {
    display: none;
  }
  .address {
    background: #64a7de;
    left: 0;
    top: 1270px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    place-content: center;
  }
  .addr_section {
    margin: 5px;
    text-align: center;
  }
  .contact_item {
    margin: 0px;
    padding: 2px;
  }
  .footer_group {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 280px;
  }
  .footer_subsection {
    margin: 3px;
    padding: 2px;
    text-align: center;
  }
  .sub a {
    font-size: 15px;
    color: rebeccapurple;
  }
  .copyright {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto;
    text-align: center;
  }
}

